import React from 'react'

export const TodoItem = ({todo, onDelete}) => {
    return (
        <>
        <div style={{width:"60%"}}>
           <h4 style={{color:'black'}}>{todo.title}</h4>
           <p style={{color:'black'}}>{todo.desc}</p>
           <button className="btn btn-sm btn-danger" onClick={()=>{onDelete(todo)}}>Delete</button> 
            </div>
        <hr/> 
        </>
    )
}
