import React, { useState } from 'react';
import pers from '../assets/personal/Todo5.jpg'


export const AddTodo = ({ addTodo }) => {
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");

    const submit = (e) => {
        e.preventDefault();
        if (!title || !desc) {
            alert("Title or Description cannot be blank");
        }
        else {
            addTodo(title, desc);
            setTitle("");
            setDesc("");
        }
    }
    return (
        <div>
            <div className='container mb-5'>
                <div className='row'>
                    <div className='col-md 2 d-flex justify-content-center'>
                        <img src={pers} alt="Contact" height="400px" width="500px"></img>
                    </div>
                    <div className='col-md-6'>
                        <form onSubmit={submit} >
                            <div class="mb-3">
                                <label for="exampleForm" class="form-label">Todo Title:</label>
                                <input type="text" class="form-control" id="title" value={title} onChange={(e) => setTitle(e.target.value)} />
                            </div>

                            <div class="mb-3">
                                <label for="exampleFormControlInput1" class="form-label">Description:</label>
                                <input type="text" class="form-control" id="desc" value={desc} onChange={(e) => setDesc(e.target.value)} />
                            </div>

                            <button type="submit" class="btn btn-sm btn-success">Add Todo</button>
                        </form>

                    </div>

                </div>
            </div>
        </div>
    );
    
    // return (
    //     <div  style={{width:'60%'}}>
    //         <h3 style={{color:'black'}} >Personal Uses</h3>
    //         <form onSubmit={submit}>
    //             <div className="mb-3">
    //                 <label htmlFor="title" className="form-label" style={{color:'black'}} >Todo Title</label>
    //                 <input type="text"  value={title} onChange={(e) => setTitle(e.target.value)} className="form-control" id="title" aria-describedby="emailHelp" />

    //             </div>
    //             <div className="mb-3">
    //                 <label htmlFor="desc" className="form-label" style={{color:'black'}} >Todo Description</label>
    //                 <input type="text" value={desc} onChange={(e) => setDesc(e.target.value)} className="form-control" id="desc" />
    //             </div>
    //             <button type="submit" className="btn btn-sm btn-success">Add Todo</button>
    //         </form>
    //     </div>
    // )
}
